  color: #8cd0ab;
}

.count-circle {
  height: 10px;
  width: 10px;
  margin-right: 5px;
  border-radius: 20%;
  background: #fff;
}

.count-wrapper {
  padding-top: 14px;
  font-size: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: left;
  padding-left: 16px;
}

.count-title {
  color: #999;
}

.count-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.count-container .number {
  padding-left: 15px;
}
